import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatExpansionPanel } from '@angular/material/expansion';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { TipoRespuestaConfiguracionModel } from 'src/app/commons/models/evaluacion-docente/mantenedores/tipo-respuesta-configuracion.model';
import { TipoRespuestaModel } from 'src/app/commons/models/evaluacion-docente/mantenedores/tipo-respuesta.model';
import { MantenedoresEvaluacionDocenteService } from 'src/app/commons/services/evaluacion-docente-services/mantenedores.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

@Component({
  selector: 'app-mantenedor-respuestas',
  templateUrl: './mantenedor-respuestas.component.html',
  styles: [
  ]
})
export class MantenedorRespuestasComponent implements OnInit {

  public listadoTipoRespuestas: TipoRespuestaModel[] = [];
  public tipoRespuestaSeleccionada: TipoRespuestaModel;
  public configRespuesta: TipoRespuestaConfiguracionModel[] = [];

  public isModoEdicion: boolean = false;
  public isModoEdicionConfig: boolean = false;
  public isEdit: boolean = false;

  public formPeriodo: FormGroup;

  //** PAGINATION PREGUNTAS */
  public searchPagination: SearchPagination<string> = null;
  public pageSize: number = 10;
  public page: number = 1;
  public totalElements = 0;
  public buscador: string = '';

  public formTopBar: FormGroup;
  public formTipoRespuesta: FormGroup;
  public formConfigRespuesta: FormGroup;
  //** */

  public loading = false;
  @BlockUI('mantenedorRespuestasEvaluacionDocente') blockUI: NgBlockUI;

  constructor(
    private mantenedorService: MantenedoresEvaluacionDocenteService,
    private toastr: ToastrService,
    private utilAlert: UtilAlert
  ) { }

  ngOnInit(): void {
    this.initFormTopBar();
    this.listarTipoRespuestas();
  }

  initFormTopBar() {
    this.formTopBar = new FormGroup({
      buscador: new FormControl(""),
      registros: new FormControl("")
    })

    this.formTopBar.get("buscador").setValue(this.buscador);
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  initFormTipoRespuesta(tipoRespuesta: TipoRespuestaModel) {
    this.formTipoRespuesta = new FormGroup({
      codigo: new FormControl("", [Validators.required]),
      descripcion: new FormControl("", [Validators.required]),
    })

    this.isEdit = tipoRespuesta != null;
    if (tipoRespuesta != null) {
      this.formTipoRespuesta.get("codigo").setValue(tipoRespuesta.codigo);
      this.formTipoRespuesta.get("descripcion").setValue(tipoRespuesta.descripcion);
    }
  }

  initFormConfigRespuesta(configRespuesta: TipoRespuestaConfiguracionModel) {
    this.formConfigRespuesta = new FormGroup({
      id: new FormControl(""),
      detalle: new FormControl("", [Validators.required]),
      valor: new FormControl("", [Validators.required]),
    })

    this.isEdit = configRespuesta != null;
    if (configRespuesta != null) {
      this.formConfigRespuesta.get("id").setValue(configRespuesta.id);
      this.formConfigRespuesta.get("detalle").setValue(configRespuesta.detalle);
      this.formConfigRespuesta.get("valor").setValue(configRespuesta.valor.toString());
    } else {
      this.formConfigRespuesta.get("id").setValue("0");
    }
  }

  listarTipoRespuestas() {
    this.listadoTipoRespuestas = [];
    if (this.searchPagination == null) {
      this.searchPagination = {
        page: this.page,
        records: this.pageSize,
        seek: this.buscador,
      }
    } else {
      this.searchPagination = null
      this.listarTipoRespuestas();
    }
    this.mantenedorService.paginationTipoRespuestas(this.searchPagination).subscribe(result => {
      this.listadoTipoRespuestas = result.content;
      this.totalElements = result.totalElements;
    })
  }

  filtrarPorCodigo(valor: string) {
    this.buscador = valor;
    this.listarTipoRespuestas();
  }

  iniciarEdicionConfigRespuesta(configRespuesta: TipoRespuestaConfiguracionModel) {
    this.initFormConfigRespuesta(configRespuesta);
    this.isModoEdicionConfig = true;
  }

  cancelarEditConfig() {
    this.isModoEdicionConfig = false;
  }

  iniciarEdicionTipoRespuesta(tipoRespuesta: TipoRespuestaModel) {
    this.initFormTipoRespuesta(tipoRespuesta);
    this.isModoEdicion = true;
  }



  saveUpdateTipoRespuesta() {
    if (!this.formTipoRespuesta.valid) {
      this.toastr.error("El formulario no es valido.");
    } else {
      this.blockUI.start("Guardando cambios...");
      let tipoRespuesta = new TipoRespuestaModel();

      tipoRespuesta.codigo = this.formTipoRespuesta.get("codigo").value.toUpperCase();
      tipoRespuesta.descripcion = this.formTipoRespuesta.get("descripcion").value;

      if (this.isEdit) {
        this.mantenedorService.updateTipoRespuesta(tipoRespuesta).subscribe(result => {
          this.volver();
          this.listarTipoRespuestas();
          this.utilAlert.infoSwal("Tipo de Respuesta editada exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      } else {
        this.mantenedorService.saveTipoRespuesta(tipoRespuesta).subscribe(result => {
          this.volver();
          this.listarTipoRespuestas();
          this.utilAlert.infoSwal("Tipo de Respuesta creada exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      }
      this.blockUI.stop();
    }
  }

  saveUpdateConfigRespuesta() {
    if (!this.formConfigRespuesta.valid) {
      this.toastr.error("El formulario no es valido.");
    } else {
      this.blockUI.start("Guardando cambios...");
      let configRespuesta = new TipoRespuestaConfiguracionModel();

      configRespuesta.id = this.formConfigRespuesta.get("id").value;
      configRespuesta.detalle = this.formConfigRespuesta.get("detalle").value;
      configRespuesta.valor = this.formConfigRespuesta.get("valor").value;
      configRespuesta.tipoRespuesta = this.tipoRespuestaSeleccionada;

      let configMasivo: TipoRespuestaConfiguracionModel[] = [configRespuesta];

      this.mantenedorService.saveAllTipoRespuestaConfiguracion(configMasivo).subscribe(result => {
        this.configRespuesta = [];
        this.getConfigByCodigo(this.tipoRespuestaSeleccionada);
        this.utilAlert.infoSwal("Tipo de Respuesta editada exitosamente.");
      }, (_error) => {
        this.toastr.error(_error?.error?.message);
      })

    }
    this.blockUI.stop();
  }


  volver() {
    this.isModoEdicion = false;
  }

  getConfigByCodigo(item: TipoRespuestaModel) {
    let cargarInfo = true;
    if (this.configRespuesta.length != 0 && this.configRespuesta[0].tipoRespuesta.codigo == item.codigo) {
      cargarInfo = false;
    }

    if (cargarInfo) {
      this.isModoEdicionConfig = false;
      this.configRespuesta = [];
      this.mantenedorService.getConfiguracionTipoRespuesta(item.codigo).subscribe(result => {
        this.configRespuesta = result;
        this.tipoRespuestaSeleccionada = item;
      })
    }
  }

}
